import {
  type NestedValues,
  mergeDeps,
  setDepValue,
  upsertIfMissing,
} from '../../../engine/signals'
import { type AttributePlugin, PluginType } from '../../../engine/types'
import { modifyCasing } from '../../../utils/text'

export const Signals: AttributePlugin = {
  type: PluginType.Attribute,
  name: 'signals',
  onLoad: (ctx) => {
    const { key, mods, value, genRX, evalRX, batch } = ctx
    const ifMissing = mods.has('ifmissing')
    const { deps, dm, rxFn } = genRX()
    if (key !== '') {
      const k = modifyCasing(key, mods)
      const v = value === '' ? value : evalRX(rxFn, dm, deps)
      batch(() => {
        if (ifMissing) {
          upsertIfMissing(k, v)
        } else {
          setDepValue(k, v)
        }
      })
    } else {
      const nv = evalRX<NestedValues>(rxFn, dm, deps)
      batch(() => {
        mergeDeps(nv, ifMissing)
      })
    }
  },
}
