import { setDep } from '../../../engine/signals'
import {
  type AttributePlugin,
  PluginType,
  Requirement,
} from '../../../engine/types'
import { modifyCasing } from '../../../utils/text'

const name = 'computed'
export const Computed: AttributePlugin = {
  type: PluginType.Attribute,
  name,
  keyReq: Requirement.Must,
  valReq: Requirement.Must,
  onLoad: ({ key, mods, genRX, computed, batch }) => {
    key = modifyCasing(key, mods)
    const { deps, rxFn } = genRX()
    const c = computed(deps, rxFn)
    batch(() => {
      setDep(key, c)
    })
  },
}
