// Icon: material-symbols:network-wifi
// Slug: Sets the indicator signal used when fetching data via SSE
// Description: must be a valid signal name

import { runtimeErr } from '../../../engine/errors'
import { Signal, upsertIfMissing } from '../../../engine/signals'
import {
  type AttributePlugin,
  PluginType,
  Requirement,
} from '../../../engine/types'
import { modifyCasing, trimDollarSignPrefix } from '../../../utils/text'
import {
  DATASTAR_SSE_EVENT,
  type DatastarSSEEvent,
  FINISHED,
  STARTED,
} from '../backend/shared'

export const Indicator: AttributePlugin = {
  type: PluginType.Attribute,
  name: 'indicator',
  keyReq: Requirement.Exclusive,
  valReq: Requirement.Exclusive,
  onLoad: (ctx) => {
    const { el, key, mods, value, batch } = ctx
    const signalName = key
      ? modifyCasing(key, mods)
      : trimDollarSignPrefix(value)
    const { dep } = batch(() => {
      return upsertIfMissing(signalName, false)
    })
    if (!(dep instanceof Signal)) {
      throw runtimeErr('not_signal', ctx, { signalName })
    }
    const watcher = ((event: CustomEvent<DatastarSSEEvent>) => {
      const { type, elId } = event.detail
      if (elId !== el.id) {
        return
      }
      switch (type) {
        case STARTED:
          dep.value = true
          break
        case FINISHED:
          dep.value = false
          break
      }
    }) as EventListener

    document.addEventListener(DATASTAR_SSE_EVENT, watcher)

    return () => {
      dep.value = false
      document.removeEventListener(DATASTAR_SSE_EVENT, watcher)
    }
  },
}
