// Authors: Delaney Gillilan
// Icon: mdi-light:vector-intersection
// Slug: Runs an expression when an element intersects with the viewport
// Description: An attribute that runs an expression when an element intersects with the viewport.

import {
  type AttributePlugin,
  PluginType,
  Requirement,
} from '../../../engine/types'
import { modifyTiming } from '../../../utils/timing'
import { modifyViewTransition } from '../../../utils/view-transtions'

export const OnIntersect: AttributePlugin = {
  type: PluginType.Attribute,
  name: 'onIntersect',
  keyReq: Requirement.Denied,
  onLoad: ({ el, rawKey, mods, genRX, evalRX }) => {
    const { dm, deps, rxFn } = genRX()
    let callback = () => evalRX(rxFn, dm, deps)
    callback = modifyTiming(callback, mods)
    callback = modifyViewTransition(callback, mods)

    const options = { threshold: 0 }
    if (mods.has('full')) {
      options.threshold = 1
    } else if (mods.has('half')) {
      options.threshold = 0.5
    }

    const observer = new IntersectionObserver((entries) => {
      for (const entry of entries) {
        if (entry.isIntersecting) {
          callback()

          if (mods.has('once')) {
            observer.disconnect()
            delete el.dataset[rawKey]
          }
        }
      }
    }, options)

    observer.observe(el)

    return () => observer.disconnect()
  },
}
